
.contact-details-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background:#130f2a;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 1.5rem;
    margin-bottom: 2rem;

}

.contact-details-card .icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    background-color: #3d3072;
    margin-bottom: 0.8rem;
}

.icon img
{
    width: 2.5rem;
    height: auto;
    object-fit: contain;
    border-radius: 0.2rem;
}

.contact-details-card p,a{
    text-decoration: none;
    color: inherit;

    font-size: 0.9rem;
    font-weight: 400;
}