.skills-container{
    margin: 4rem 0;
    position: relative;

}
.skills-container h5{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
    
}
.skills-container::after,
.skills-container::before{
    content:"";
    
    width: 28rem;
    height: 28rem;
    border-radius: 28.125rem;
    background:#7c66e3;
    position: absolute;
    z-index:-1;
    filter: blur(160px);

}
.skills-container::after{
    top: -3rem;
    left: -5rem;
}
.skills-container::before{
    background: #9e51bb;
    bottom:0rem;
    right: 0rem;

}
.skills-content{
    display: flex;
    align-items: flex-start;
    gap: 3rem;
}
.skills{
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);

}

.skills-info{
    flex: 1;
}

@media (max-width: 1025px)
{
    .skills{
        padding-left:1rem;
    }

    .skills,
    .skills-content{
        grid-gap:2rem;
    }
}

@media (max-width:768px){
    .skills-content{
        flex-direction: column-reverse;
        font-palette: 3re,;
    }
    .skills,
    .skills-info{
        width: 100%;
    }
    .skills-container h5{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
    .skills-container::after,
    .skills-container::before{
        width: 18rem;
        height: 18rem;
    }
  
}

@media (max-width:600px){
    .skills-container{
        padding: 0;
    }
}


