.skills-card{
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 2.5rem;
    text-align: center;
    background-color: rgba(22,17,47,0.390);
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;

}
.skills-card:hover,
.skills-card.active{
    background: linear-gradient(180deg, #aa94fe 0%, #7c5fe6 100%);
}

.skill-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content:center ;
    background:#ffffff;
    border: 1.5px solid #6853ba;
    position: absolute;
    top: -1rem;
    left: -1rem;
    border-radius: 0.65rem;

}

.skill-icon img{
    width: 3.5rem;
    height: auto;
    object-fit: contain;
    border-radius: 0.15rem;
}

@media (max-width:1025px)
{
    .skills-card{
        padding: 2rem;
    }
    .skills-card span{
        font-size: 1rem;
    }
    .skill-icon{
        width: 3rem;
        height: 3rem;

    }
    .skill-icon img{
        width: 1.8rem;
    }
}
.skills-card span{
    font-size: 0.9rem;

}

@media (max-width: 768px)
{
    .skills-card span{
        font-size: 0.8rem;

    }
}
